.container {
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;

  @media screen and (min-width: $mobile) {
    width: 320px;
  }

  @media screen and (min-width: $tablet) {
    width: 768px;

    .footer & {
      display: flex;
      align-items: center;
    }
  }

  @media screen and (min-width: $desktop) {
    width: 1314px;
  }
}
